/* src/app/features/application/applications/applications.css */
#customers {
  font-family:
    Arial,
    Helvetica,
    sans-serif;
  border-collapse: collapse;
  width: 100%;
  background-color: white;
  margin-top: 10px;
}
#customers td,
#customers th {
  border: 1px solid #ddd;
  padding: 8px;
}
#customers tr:nth-child(even) {
  background-color: #f2f2f2;
}
#customers tr:hover {
  background-color: #ddd;
}
#customers th {
  padding-top: 12px;
  padding-bottom: 12px;
  text-align: left;
  background-color: #006192;
  color: white;
}
.actions-card {
  border: none;
  border-radius: 2px;
  background-color: white;
  box-shadow: none;
}
